
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import {refreshMixin} from '../../common/js/mixin'
import { mapState } from "vuex";
import {backMixin} from "../../common/js/picBack";
export default {
name: "ReceiveInfo",
mixins: [refreshMixin,backMixin],
components: {
XHeader,
Group,
Cell,
Previewer
},
mounted() {
this.id = this.$route.query.id || this.$route.params.id;
this.getData()
this.$bus.on('refreshReceiveList',()=>{
this.getData()
})
},
beforeDestroy(){
this.$bus.off('refreshReceiveList')
},
filters: {

picUrl(path, w = 0, h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
}
},
data() {
return {
id: '',
info: {
InvoiceDate: ''
},
showImgFile:[],
payMethod: ['未付', '支票', '网上银行', '电付', '邮局汇款', '支付宝', '其他'],
isShow: false,
titleShow: true,
options: {
saveImg(el) {
console.log(123);
}
}
}
},
activated() {
this.titleShow = true
},
computed: {
...mapState(["isMyClient", "isAdmin"])
},
methods: {
getPayType() {
return this.payMethod[this.info.PayType - 1]
},
getData() {
axios.get(`/Cost/GetCrmReturnedMoneyByID/${this.id}`).then(res => {
if (res.data.State === 1) {
this.info = res.data.Data
if(this.info.ImgAttach){
for (let i = 0; i < this.info.ImgAttach.length; i++) {
let item = this.info.ImgAttach[i];
this.showImgFile.push({
src: `${api50}/api/v1/File/DownLoadPic?filePath=${item.FilePath}`
});
}
}
} else {
this.$vux.toast.show({
text: res.data.ErrorMessage,
type: 'warn'
})
}
}).catch(e => {
this.$vux.toast.show({
text: '网络异常',
type: 'warn'
})
})
},
goto() {

this.$router.push(`/changeReceive?id=${this.id}`)
},
isDel(val) {
return (this.isAdmin || (val ? (new Date().getTime() - new Date(val.replace(/-/g, "/")).getTime() <= 86400000) : ''));
},
del() {
const _this = this 
this.$vux.confirm.show({
title: '删除汇款记录',
content: '确认删除回款记录?',

onCancel() {
},
onConfirm() {
axios.post(`/Cost/DeleteCrmReturnedMoney/${_this.id}`).then(res => {
if (res.data.State === 1) {
_this.$vux.toast.show({
text: '删除成功!',
type: 'success'
})
setTimeout(() => {
_this.$vux.toast.hide()
_this.$router.back()
},500)
_this.$bus.emit('refreshReceiveList')
}
}).catch(e => {

})
}
})
}
}
}



